import React, { useState } from 'react';
import Board from './TicTacToe';
import Star from './Star';
import SmallMath from './SmallMath';
import './styles.css';
import { MultipleTabs, useLocalStorageState } from './utils';
let IDs = {};

function Login() {
  const [loggedInId, setLoggedInId] = useState(0);
  const [loginKey, setLoginKey] = useLocalStorageState('FTLoginKey','');
  const refLoginID = React.useRef();
  let retElement = null;

  React.useEffect(() => {
    IDs[5762] = {id: 'FTAk', elem: <Star />};
    IDs[7852] = {id: 'FTSa', elem: <SmallMath />};
    setLoginKey('');
  }, [setLoginKey]);

  function checkLogin(event) {
    event.preventDefault();
    const login = refLoginID.current.value;
    if (login in IDs) 
    {
      setLoggedInId(login);
      setLoginKey(IDs[login]?.id);
    }
    else 
    {
      setLoggedInId(0);
      setLoginKey('');
    }
  }

  React.useEffect(() => {
    refLoginID?.current?.focus();
  }
  );

  switch (loggedInId) {
    case 0:
    retElement = (
      <form>
        <div>
          <input ref={refLoginID} className="login" />
          <button type="submit" onClick={checkLogin}>Login</button>
        </div>
      </form>);
      break;
    default : 
    retElement = IDs[loggedInId]?.elem;
  }
  return (retElement);
}

export default function App() {
  const [selTile, setSelTile] = useState(0);

  const tabs = [
    {key: 0, displayName: "Tic Tac Toe", displayComponent: <Board />},
    {key: 1, displayName: "Login", displayComponent: <Login />}
  ];

const props = {
  title: "Welcome",
  selection: selTile,
  onClick: setSelTile,
  buttonClass: "tile1",
  returnButtonClass: "retButton1",
  returnDisplay: `<< Home page`,
  tabs: tabs
}
  
  return (
    <MultipleTabs {...props} />
  )
}
